import React, { Component } from 'react';
import AboutAside from './about/about_aside.component';
import AboutStaticContent from './about/about_static_content.component';
import Animate, { AnimateGroup, AnimateItem, AnimateCanvas } from 'components/animate';

export default class About extends Component{

  state = {
    finished: 0
  }

  render() {
    return(
      <section id="cinema-efficiency">
        <div id="about-wrapper">
          <div id="about">
            <AboutStaticContent/>
            <AboutAside/>
          </div>
        </div>
        <Subtitles isMobile={ this.props.isMobile }/>
      </section>
    )
  }
}

const Subtitles = ({ isMobile }) => (
  <div id="subtitles">
    <Animate id="animate-text" height={ !isMobile ? 27000 : 22500 }>
      <AnimateGroup fadeOut={{point: 2000, length: 1000}}>
        <AnimateItem fadeIn={{point: 0, length: 250 }}>
            <span>&nbsp;</span>
        </AnimateItem>
        <AnimateItem fadeIn={{point: 250, length: 250 }}>
            <span>&nbsp;</span><br className="br-m"/>
        </AnimateItem>
        <AnimateItem fadeIn={{point: 500, length: 250 }}>
            <span>&nbsp;</span>
        </AnimateItem>
        <AnimateItem  fadeIn={{point: 750, length: 250 }}>
            <span></span>
        </AnimateItem>
        <AnimateItem fadeIn={{point: 1000, length: 250 }}>
            <span>   .</span>
        </AnimateItem>
      </AnimateGroup>

      <AnimateGroup fadeOut={{point: 5500, length: 1000}}>
          <AnimateItem fadeIn={{point: 3500, length: 250 }}>
              <span>&nbsp;</span>
          </AnimateItem>
          <AnimateItem fadeIn={{point: 3750, length: 250 }}>
              <span>&nbsp;</span><br className="br-m"/>
          </AnimateItem>
          <AnimateItem fadeIn={{point: 4000, length: 250 }}>
              <span>&nbsp;</span>
          </AnimateItem>
          <AnimateItem fadeIn={{point: 4250, length: 250 }}>
              <span>,</span>
          </AnimateItem>
          <AnimateItem fadeIn={{point: 4500, length: 250 }}>
              <span>    .</span>
          </AnimateItem>
      </AnimateGroup>

      <AnimateGroup fadeOut={{point: 9000, length: 1000}}>
          <AnimateItem fadeIn={{point: 7000, length: 250 }}>
              <span> </span><br className="br-m"/>
          </AnimateItem>
          <AnimateItem fadeIn={{point: 7250, length: 250 }}>
              <span>&nbsp;</span>
          </AnimateItem>
          <AnimateItem fadeIn={{point: 7500, length: 250 }}>
              <span>&nbsp;</span><br className="br-m"/>
          </AnimateItem>
          <AnimateItem fadeIn={{point: 7750, length: 250 }}>
              <span>&nbsp;</span>
          </AnimateItem>
          <AnimateItem fadeIn={{point: 8000, length: 250 }}>
              <span></span>
          </AnimateItem>
      </AnimateGroup>

      <AnimateGroup fadeOut={{point: 12250, length: 1000}}>
        <AnimateItem fadeIn={{point: 10500, length: 250 }}>
          <span>,&nbsp;</span><br className="br-m"/>
        </AnimateItem>
       <AnimateItem fadeIn={{point: 10750, length: 250 }}>
           <span>,&nbsp;</span><br className="br-m"/>
       </AnimateItem>
        <AnimateItem fadeIn={{point: 11000, length: 250 }}>
          <span>,</span><br className="br-m"/>
        </AnimateItem>
        <AnimateItem fadeIn={{point: 11250, length: 250 }}>
          <span>
            ,    <br className="br-m"/> 
                 <br className="br-m"/>  
                 .
          </span>
        </AnimateItem>
      </AnimateGroup>

      <AnimateGroup fadeOut={{point: 16000, length: 1000}}>
        <AnimateItem fadeIn={{point: 14000, length: 250}}>
          <span>
             ,  <br className="br-m"/> 
                  <br className="br-m"/> 
             -
          </span>
        </AnimateItem>
      </AnimateGroup>

      <AnimateCanvas 
        zoom={{point: 17750, length: 3000}} 
        fadeIn={[
          {
            text: '  ',
            point: 14250,
            length: 250
          },
          {
            text: '  .',
            point: 14500,
            length: 250
          },
          {
            text: '  ..',
            point: 14750,
            length: 250
          },
          {
            text: '  ...',
            point: 15000,
            length: 250
          }
        ]}
        isMobile={ isMobile }
      > 
      </AnimateCanvas>
    </Animate>
  </div>    
)