import m_audience_age from 'assets/images/mobile/audience_age.svg'
import m_audience_groups from 'assets/images/mobile/audience_groups.svg'

export default class StatsArticle3 extends Component{
  render() {
    return(
      <article id="stats--article-3" className="stats--article-3 stats--article">
        <div className="text-left-container">
          <h1 className="bebas-64 m-bebas-30">: </h1>
          {
            this.props.device === 'desktop' ?
            <DesktopGraphs/> :
            <MobileGraphs/>
          }
          <p className="helvetica-12-bold m-helvetica-12-bold">
               25-34      (31%).
            <br/><br/>
             2      18-24  (25%).
            <br/><br/>
              35-44   18%.
            <br/><br/><br/>
                 56%      .
            <br/><br/>
                  ,     35 , ,    60%.
            <br/><br/><br/>
                  (33,1%).          25 .
            <br/><br/>
               29,5%.       25-34  (42%   ),      18-24  (36%).
            <br/><br/>
                  30,1%,      42%.       35- .
            <br/><br/>
            5%       .
            <br/><br/><br/>
            <span className="helvetica-14-bold m-helvetica-14-bold">
              : <a href="http://ekinobilet.fond-kino.ru/issledovanie-kinoauditorii/" target="_blank"><u> </u></a>
            </span>
          </p>
        </div>
        <div className="text-right-container helvetica-12-bold m-helvetica-12-bold">
            <div>
              <h3 className="bebas-25">  </h3>
              <p>
                  <br/>
                     
              </p>
              <img src={ this.props.device === 'desktop' ? poll : m_poll }/>
            </div>

            <div>
              <h3 className="bebas-25">
                  2014   <br/>
                17    <br/>
                  500 . .
              </h3>
              {
                this.props.device === 'desktop' ?
                <Slider
                id="movie-slider"
                slideWidthVw={14.921875}
                visible="true"
                content={[
                  <img src={ movie_0 } />,
                  <img src={ movie_1 } />,
                  <img src={ movie_2 } />,
                  <img src={ movie_4 } />,
                  <img src={ movie_5 } />,
                  <img src={ movie_6 } />,
                  <img src={ movie_7 } />,
                  <img src={ movie_8 } />,
                  <img src={ movie_9 } />,
                  <img src={ movie_10 } />,
                  <img src={ movie_11 } />,
                  <img src={ movie_12 } />,
                  <img src={ movie_13 } />,
                ]}/> : null
              }
              <span>
                 , <br/>
              <Link to="/movies"><u> 500 . </u></Link>
              </span>
              <span className="helvetica-14-bold">
                :
                <u><a href="http://www.kinometro.ru/kino/analitika" target="_blank"> kinometro</a></u>
              </span>
            </div>
        </div>
      </article>
    )
  }
}

const DesktopGraphs = () => (
  <Fragment>
    <Slider
      id="desktop-stats-slider"
      finite={true}
      slideWidthVw={42.8125}
      slideNames={[
        '2019',
        '2020'
      ]}
    >
      <div>
        <p>
           2019    . <br/>
             29 .
        </p>
        <img src={audience_2019}/>
      </div>
      <div>
        <p>
           2020      <br/>
                 COVID-19.
        </p>
        <img src={audience_2020}/>
      </div>
    </Slider>
    <div>
      <h3>    2019 </h3>
      <img src={audience_age} alt=""/>
      <img src={audience_groups} alt=""/>
    </div>
  </Fragment>
);

const MobileGraphs = () => {

  const [slide, setSlide] = React.useState(0);
  const onSlideChange = slide => {
    setSlide(slide);
  };

  return(
    <Fragment>
      {
        slide < 2 ?
        <p>
           2019    .<br/>
             29 .
        </p> :
        <p>
           2020    <br/>
              <br/>
             COVID-19.
        </p>
      }
      <Slider
        onSlideChange={onSlideChange}
        id="mobile-stats-slider-1"
        slideWidthVw={375/3.75}
        slideNames={[
          '   2019',
          '   2019',
          '   2020',
        ]}
      >
        <div>
          <h3>
               2019 <br/>
            <span>(, . .)</span>
          </h3>
          <img src={ m_audience_2019_1 } />
        </div>

        <div>
          <h3>
               2019 <br/>
            <span>(, . .)</span>
          </h3>
          <img src={ m_audience_2019_2 } />
        </div>

        <div>
          <h3>
               2020 <br/>
            <span>(, . .)</span>
          </h3>
          <img src={ m_audience_2020 } />
        </div>
      </Slider>

      <Slider
        id="mobile-stats-slider-2"
        slideWidthVw={375/3.75}
        slideNames = {[
          ' ',
          ' ',
        ]}
      >
        <div>
          <h3>   <br/>  2019 </h3>
          <img src={ m_audience_age } />
        </div>
        <div>
          <h3>   <br/>  2019 </h3>
          <img src={ m_audience_groups } />
        </div>
      </Slider>
    </Fragment>
  );
}
